.App{
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.getImage{
    display: flex;
}

.item{
    min-height: 100px;
    min-width: 8.5rem;
    padding: 14px;
}

.item img{
    width: 100%;
    height: 60%;
    border-radius: 5rem;
    pointer-events: none;
}

.carousel{
    cursor:grab;
    overflow: hidden;
    background-color: #7B87C2;
}