footer{
    background: #000080;
    width: 100%;
    bottom: 0;
    left: 0;
    box-sizing: border-box;
  }
  footer .rodape{
    max-width: 1250px;
    margin: auto;
    padding: 30px 40px 40px 40px;
  }
  footer .rodape .top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  .rodape .top .logoo{
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 30px;
  }
  .rodape .top .media-icons{
    display: flex;
  }
  .rodape .top .media-icons a{
    height: 40px;
    width: 40px;
    margin: 0 8px;
    border-radius: 50%;
    text-align: center;
    line-height: 40px;
    color: #fff;
    font-size: 17px;
    text-decoration: none;
    transition: all 0.4s ease;
  }
  .top .media-icons a:nth-child(1){
    background: #4267B2;
  }
  .top .media-icons a:nth-child(1):hover{
    color: #4267B2;
    background: #fff;
  }
  .top .media-icons a:nth-child(2){
    background: #1DA1F2;
  }
  .top .media-icons a:nth-child(2):hover{
    color: #1DA1F2;
    background: #fff;
  }
  .top .media-icons a:nth-child(3){
    background: #E1306C;
  }
  .top .media-icons a:nth-child(3):hover{
    color: #E1306C;
    background: #fff;
  }
  .top .media-icons a:nth-child(4){
    background: #0077B5;
  }
  .top .media-icons a:nth-child(4):hover{
    color: #0077B5;
    background: #fff;
  }
  footer .rodape .link-boxes{
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  footer .rodape .link-boxes .box{
    width: calc(100% / 5 - 10px);
  }
  .rodape .link-boxes .box .link_name{
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 10px;
    position: relative;
  }
  .link-boxes .box .link_name::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: -2px;
    height: 2px;
    width: 35px;
    
  }
  .rodape .link-boxes .box li{
    margin: 6px 0;
    list-style: none;
  }
  .rodape .link-boxes .box li a{
    color: #fff;
    font-size: 14px;
    font-weight: 400;
    text-decoration: none;
    opacity: 0.8;
    transition: all 0.4s ease
  }
  .rodape .link-boxes .box li a:hover{
    opacity: 1;
    text-decoration: underline;
  }
  .rodape .link-boxes .input-box{
    margin-right: 55px;
  }
  .link-boxes .input-box input{
    height: 40px;
    width: calc(100% + 55px);
    outline: none;
    border: 2px solid #AFAFB6;
    background: #140B5C;
    border-radius: 4px;
    padding: 0 15px;
    font-size: 15px;
    color: #fff;
    margin-top: 5px;
  }
  .link-boxes .input-box input::placeholder{
    color: #AFAFB6;
    font-size: 16px;
  }
  .link-boxes .input-box input[type="button"]{
    background: #fff;
    color: #140B5C;
    border: none;
    font-size: 18px;
    font-weight: 500;
    margin: 4px 0;
    opacity: 0.8;
    cursor: pointer;
    transition: all 0.4s ease;
  }
  .input-box input[type="button"]:hover{
    opacity: 1;
  }
  footer .bottom-details{
    width: 100%;
    background: rgb(3, 2, 5);
  }
  footer .bottom-details .bottom_text{
    max-width: 1250px;
    margin: auto;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
  }
  .bottom-details .bottom_text span,
  .bottom-details .bottom_text a{
    font-size: 14px;
    font-weight: 300;
    color: #fff;
    opacity: 0.8;
    text-decoration: none;
  }
  .bottom-details .bottom_text a:hover{
    opacity: 1;
    text-decoration: underline;
  }
  .bottom-details .bottom_text a{
    margin-right: 10px;
  }

  .logoo p {
    font-family: 'Playfair Display', serif;
    font-size: 2rem;
    color: #fff;
  }

  .logotipo {
    width: 3rem;
    height: auto;
  }

  @media (max-width: 900px) {
    footer .rodape .link-boxes{
      flex-wrap: wrap;
    }
    footer .rodape .link-boxes .input-box{
      width: 40%;
      margin-top: 10px;
    }
  }

  @media (max-width: 700px){
    footer{
      position: relative;
    }
    .rodape .top .logoo{
      font-size: 26px;
    }
    .rodape .top .media-icons a{
      height: 35px;
      width: 35px;
      font-size: 14px;
      line-height: 35px;
    }
    footer .rodape .link-boxes .box{
      width: calc(100% / 3 - 10px);
    }
    footer .rodape .link-boxes .input-box{
      width: 60%;
    }
    .bottom-details .bottom_text span,
    .bottom-details .bottom_text a{
      font-size: 12px;
    }
  }

  @media (max-width: 520px){
    footer::before{
      top: 145px;
    }
    footer .rodape .top{
      flex-direction: column;
    }
    .rodape .top .media-icons{
      margin-top: 16px;
    }
    footer .rodape .link-boxes .box{
      width: calc(100% / 2 - 10px);
    }
    footer .rodape .link-boxes .input-box{
      width: 100%;
    }
  }

  @media (max-width: 380px){
    footer::before{
      top: 145px;
    }
    footer .rodape .top{
      flex-direction: column;
    }
    .rodape .top .media-icons{
      margin-top: 16px;
    }
    footer .rodape .link-boxes .box{
      width: calc(100% / 2 - 10px);
    }
    footer .rodape .link-boxes .input-box{
      width: 100%;
    }

    .rodape .link-boxes .box .link_name {
        color: #fff;
        font-size: 0.65rem;
        font-weight: 400;
        margin-bottom: 10px;
        position: relative;
    }

    .rodape .link-boxes .box li a {
        color: #fff;
        font-size: 0.5rem;
        font-weight: 400;
        text-decoration: none;
        opacity: 0.8;
        transition: all 0.4s ease;
    }
  }